// footer start
html,body{
    height: 100%;
    margin: 0;
}
body {
    display: flex; /**/
    flex-direction: column; /**/
  }
main{
        flex-grow: 1; /**/
        background-color: $darkest;
}
// footer end
a{
    display: block;
    color: inherit;
    &:hover{
        color: inherit;
    }
}

img{
    max-width: 100%;
    height: auto;
    display: block;
}
h1, h2, h3, h4, h5, h6,ul,ol{
    margin: 0;
}

ol, ul {
	list-style: none;
    padding-left: 0;
}